<template>
  <v-container fluid style="min-height: 0;" grid-list-lg >
    <v-layout row >
      <v-flex xs12 sm10 offset-sm1>
        <v-toolbar >
          <v-btn to="/" primary>Back</v-btn>
          <v-btn
            @click.native="dialog = true"
            absolute
            dark
            right
            color="orange"
          >
          <v-icon>add</v-icon>
        </v-btn>
        </v-toolbar>
        <v-card>

          <v-list> three-line
            <template v-for="item in items">
              <v-list-tile :key="item.name" @click="itemTap">

                <v-list-tile-avatar>
                  <v-checkbox hide-details v-model="selected"></v-checkbox>
                </v-list-tile-avatar>
                <v-tile-content>
                <v-list-tile-title v-html="item.name"></v-list-tile-title>
                <v-tile-sub-title v-html="item.url"></v-tile-sub-title>
                </v-tile-content>
              </v-list-tile>
            </template>
          </v-list>

        </v-card>

        <v-dialog
          v-model="dialog"
          persistent
          dark
          transition="dialog-bottom-transition"
          scrollable
        >
          <v-card>
            <v-card-title>Add News source request</v-card-title>
            <v-card-text>
            <add-source-form ref="addSourceForm"></add-source-form>
            </v-card-text>
              <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click.native="dialog = false">Close</v-btn>
              <v-btn color="blue darken-1" flat @click.native="submitForm">Submit</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import AddSourceForm from "./AddSourceForm";

export default {
  name: "SourcesList",
  components:{
    AddSourceForm
  },
  data(){
    return {
      dialog: false,
      category:[]
      }
  },
  computed:{
    items(){
      return this.$store.state.sources
    }
  },
  methods: {
    itemTap(e){
      const config = {}

      this.$store.state.sources.forEach((item)=>{
        config[item.name] = { ...item }
      })

      this.$store.setSourcesConfig(config)

    },
    submitForm(e){
      if ( this.$refs.addSourceForm.$refs.form.validate() ){

        this.$refs.addSourceForm.submit(e);

        this.dialog = false
        this.$refs.addSourceForm.clearForm(e);
      }

    },
  },
  created() {
    this.$store.dispatch('fetchSources');
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.name {
    position: absolute;
    top: 12px;
    right: 20px;
    color: grey;
}
</style>



